'use client';

import React, { useEffect, useState, useMemo } from "react";
import { Search, Eye, Loader2 } from "lucide-react";
import Link from "next/link";
import { API_URL } from "../api/apiURL";

/* --------------------------------------- */
/* USER AVATAR */
/* --------------------------------------- */
const UserAvatar = ({ user }) => {
  const initials =
    (user.firstName?.[0] || "").toUpperCase() +
    (user.lastName?.[0] || "").toUpperCase();

  return (
    <div className="flex items-center gap-3">
      {user.profileImage ? (
        <img
          src={user.profileImage}
          className="w-10 h-10 rounded-full object-cover"
          alt="profile"
        />
      ) : (
        <div className="w-10 h-10 bg-gray-300 rounded-full flex items-center justify-center text-sm font-semibold">
          {initials}
        </div>
      )}
      <div>
        <p className="font-semibold text-gray-900 text-sm">
          {user.firstName} {user.lastName}
        </p>
        <p className="text-xs text-gray-500">
          #{user.id || user._id?.slice(-6)} / {user.gender || "Not Mentioned"}
        </p>
      </div>
    </div>
  );
};

/* --------------------------------------- */
/* STATUS COLORS */
/* --------------------------------------- */
const getStatusDot = (status) =>
  ({
    approved: "bg-green-500",
    pending: "bg-yellow-500",
    reject: "bg-red-500",
  }[status] || "bg-gray-400");

const getStatusText = (status) =>
  ({
    approved: "text-green-600",
    pending: "text-yellow-600",
    reject: "text-red-600",
  }[status] || "text-gray-600");

/* --------------------------------------- */
/* SMALL DOCUMENT POPUP */
/* --------------------------------------- */
const DocumentPopup = ({ user, onClose }) => {
  if (!user) return null;

  const aFront = user?.adhaarCard?.frontImage;
  const aBack = user?.adhaarCard?.backImage;
  const pan = user?.panCard?.frontImage;

  const closeOnBg = (e) => {
    if (e.target.id === "doc-popup-bg") onClose();
  };

  return (
    <div
      id="doc-popup-bg"
      onClick={closeOnBg}
      className="fixed inset-0 bg-black/30 flex items-center justify-center z-[999]"
    >
      <div className="bg-white w-[260px] rounded-xl shadow-lg p-4 animate-fadeIn border">

        <h3 className="text-center font-semibold text-sm mb-3">Documents</h3>

        <div className="space-y-3 overflow-y-auto max-h-[300px] px-1">

          {aFront && (
            <div className="w-full border rounded-xl shadow p-2">
              <img src={aFront} className="w-full rounded-md" />
              <p className="text-center text-xs mt-1 font-medium">Aadhar Front</p>
            </div>
          )}

          {aBack && (
            <div className="w-full border rounded-xl shadow p-2">
              <img src={aBack} className="w-full rounded-md" />
              <p className="text-center text-xs mt-1 font-medium">Aadhar Back</p>
            </div>
          )}

          {pan && (
            <div className="w-full border rounded-xl shadow p-2">
              <img src={pan} className="w-full rounded-md" />
              <p className="text-center text-xs mt-1 font-medium">PAN Card</p>
            </div>
          )}

          {!aFront && !aBack && !pan && (
            <p className="text-center text-gray-500 text-xs">
              No Documents Uploaded
            </p>
          )}
        </div>

        <button
          onClick={onClose}
          className="w-full mt-3 bg-gray-900 text-white rounded-lg py-1.5 text-xs"
        >
          Close
        </button>
      </div>
    </div>
  );
};

/* --------------------------------------- */
/* MAIN COMPONENT */
/* --------------------------------------- */
export default function UserModerationDashboard() {
  const [users, setUsers] = useState([]);
  const [popupUser, setPopupUser] = useState(null);

  const [search, setSearch] = useState("");
  const [status, setStatus] = useState("Status");
  const [gender, setGender] = useState("Gender");

  const [loading, setLoading] = useState(true);

  const [currentPage, setCurrentPage] = useState(1);
  const perPage = 10;

  const [counts, setCounts] = useState({
    total: 0,
    pending: 0,
    approved: 0,
    rejected: 0,
  });

  /* --------------------------------------- */
  /* FETCH API */
  /* --------------------------------------- */
  useEffect(() => {
    const loadUsers = async () => {
      try {
        const res = await fetch(`${API_URL}/admin/user-verify`);
        const json = await res.json();

        setUsers(json.data);

        setCounts({
          total: json.data.length,
          pending: json.data.filter((u) => u.adminApprovel === "pending").length,
          approved: json.data.filter((u) => u.adminApprovel === "approved").length,
          rejected: json.data.filter((u) => u.adminApprovel === "reject").length,
        });
      } catch (e) {
        console.log("API ERROR", e);
      } finally {
        setLoading(false);
      }
    };

    loadUsers();
  }, []);

  /* --------------------------------------- */
  /* FILTER LOGIC */
  /* --------------------------------------- */
  const filtered = useMemo(() => {
    let data = [...users];

    if (search) {
      data = data.filter(
        (u) =>
          u.firstName?.toLowerCase().includes(search.toLowerCase()) ||
          u.lastName?.toLowerCase().includes(search.toLowerCase()) ||
          u.id?.toLowerCase().includes(search.toLowerCase()) ||
          u._id?.includes(search)
      );
    }

    if (status !== "Status") {
      data = data.filter((u) => u.adminApprovel === status);
    }

    if (gender !== "Gender") {
      data = data.filter((u) => u.gender === gender);
    }

    return data;
  }, [users, search, status, gender]);

  /* --------------------------------------- */
  /* PAGINATION */
  /* --------------------------------------- */
  const totalPages = Math.ceil(filtered.length / perPage);
  const pageData = filtered.slice((currentPage - 1) * perPage, currentPage * perPage);

  return (
    <div className="px-6 py-6">

      {/* CARDS */}
      <div className="flex justify-center gap-12 mb-8">

        {[
          ["Total Request This Week", counts.total],
          ["Pending Verification", counts.pending],
          ["Approved This Week", counts.approved],
          ["Rejected Due To Mismatch", counts.rejected],
        ].map(([label, count]) => (
          <div
            key={label}
            className="w-[220px] text-center border shadow rounded-2xl p-6 bg-white"
          >
            <p className="font-semibold text-sm">{label}</p>
            <h2 className="text-3xl font-black mt-3">{count}</h2>
          </div>
        ))}

      </div>

      {/* MAIN TABLE WRAPPER */}
      <div className="max-w-7xl mx-auto p-5 border rounded-2xl bg-white shadow">

        {/* FILTER BOX */}
        <div className="border rounded-xl p-3 flex justify-between items-center">

          <div className="relative w-[300px]">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400 w-5" />
            <input
              placeholder="Search By User ID"
              value={search}
              onChange={(e) => {
                setSearch(e.target.value);
                setCurrentPage(1);
              }}
              className="w-full pl-10 pr-3 py-2 border rounded-lg text-sm"
            />
          </div>

          <div className="flex gap-3">
            <select
              value={status}
              onChange={(e) => {
                setStatus(e.target.value);
                setCurrentPage(1);
              }}
              className="border px-3 py-2 bg-gray-100 rounded-md"
            >
              <option>Status</option>
              <option value="approved">Approved</option>
              <option value="pending">Pending</option>
              <option value="reject">Rejected</option>
            </select>

            <select
              value={gender}
              onChange={(e) => {
                setGender(e.target.value);
                setCurrentPage(1);
              }}
              className="border px-3 py-2 bg-gray-100 rounded-md"
            >
              <option>Gender</option>
              <option>Male</option>
              <option>Female</option>
            </select>
          </div>

        </div>

        {/* TABLE */}
        <div className="overflow-x-auto mt-5 border rounded-xl">
          {loading ? (
            <div className="flex justify-center py-10">
              <Loader2 className="animate-spin w-6 h-6" />
            </div>
          ) : (
            <table className="w-full text-sm">

              <thead className="bg-gray-100">
                <tr>
                  <th className="px-4 py-3 text-left">Reported User</th>
                  <th className="px-4 py-3 text-left">Report Date</th>
                  <th className="px-4 py-3 text-left">Documents Submitted</th>
                  <th className="px-4 py-3 text-left">Status</th>
                  <th className="px-4 py-3 text-left">Actions</th>
                </tr>
              </thead>

              <tbody className="divide-y">
                {pageData.map((user) => (
                  <tr key={user._id} className="hover:bg-gray-50">

                    <td className="px-4 py-3">
                      <UserAvatar user={user} />
                    </td>

                    <td className="px-4 py-3">
                      {new Date(user.createdAt).toLocaleDateString("en-GB", {
                        day: "2-digit",
                        month: "long",
                        year: "numeric",
                      })}
                    </td>

                    <td className="px-4 py-3">
                      {user.adhaarCard?.frontImage ||
                      user.adhaarCard?.backImage ||
                      user.panCard?.frontImage
                        ? "Documents Uploaded"
                        : "No Document Submitted"}
                    </td>

                    <td className="px-4 py-3">
                      <div className="flex items-center gap-2">
                        <div
                          className={`w-3 h-3 rounded-full ${getStatusDot(
                            user.adminApprovel
                          )}`}
                        ></div>
                        <span className={getStatusText(user.adminApprovel)}>
                          {user.adminApprovel}
                        </span>
                      </div>
                    </td>

                    <td className="px-4 py-3">
                      <button
                        onClick={() => setPopupUser(user)}
                        className="flex items-center gap-1 text-blue-700"
                      >
                        <Eye size={14} /> View
                      </button>
                    </td>

                  </tr>
                ))}
              </tbody>

            </table>
          )}
        </div>

        {/* PAGINATION (Screenshot Style) */}
        <div className="flex justify-center mt-4 items-center gap-3">

          {/* Prev */}
          <button
            onClick={() => setCurrentPage((p) => Math.max(1, p - 1))}
            disabled={currentPage === 1}
            className="text-gray-700 disabled:opacity-40 text-md"
          >
            ‹ Prev
          </button>

          {/* Page numbers */}
          <div className="flex items-center gap-2 text-sm">
            {[1, 2, 3, 4].map((page) =>
              page <= totalPages ? (
                <button
                  key={page}
                  onClick={() => setCurrentPage(page)}
                  className={`${
                    currentPage === page
                      ? "font-bold text-black underline"
                      : "text-gray-600"
                  }`}
                >
                  {page}
                </button>
              ) : null
            )}

            {totalPages > 4 && <span className="text-gray-500">.....</span>}

            {totalPages > 4 && (
              <button
                onClick={() => setCurrentPage(totalPages)}
                className={`${
                  currentPage === totalPages
                    ? "font-bold text-black underline"
                    : "text-gray-600"
                }`}
              >
                {totalPages}
              </button>
            )}
          </div>

          {/* Next */}
          <button
            onClick={() => setCurrentPage((p) => Math.min(totalPages, p + 1))}
            disabled={currentPage === totalPages}
            className="text-gray-700 disabled:opacity-40 text-md"
          >
            Next ›
          </button>
        </div>

      </div>

      {/* POPUP */}
      {popupUser && (
        <DocumentPopup user={popupUser} onClose={() => setPopupUser(null)} />
      )}

    </div>
  );
}
