'use client';

import React, { useEffect, useState, useMemo } from "react";
import { Search, Eye, Loader2, ChevronUp, ChevronDown } from "lucide-react";
import { API_URL } from "../api/apiURL";

/* --------------------------------------- */
/* USER AVATAR */
/* --------------------------------------- */
const UserAvatar = ({ user }) => {
  const initials =
    (user.firstName?.[0] || "").toUpperCase() +
    (user.lastName?.[0] || "").toUpperCase();

  return (
    <div className="flex items-center gap-3">
      {user.profileImage ? (
        <img
          src={user.profileImage}
          className="w-10 h-10 rounded-full object-cover border"
          alt="profile"
        />
      ) : (
        <div className="w-10 h-10 bg-gray-300 rounded-full flex items-center justify-center text-sm font-semibold">
          {initials}
        </div>
      )}
      <div>
        <p className="font-semibold text-gray-900 text-sm">
          {user.firstName} {user.lastName}
        </p>
        <p className="text-xs text-gray-500">
          #{user.id || user._id?.slice(-6)} / {user.gender || "Not Mentioned"}
        </p>
      </div>
    </div>
  );
};

/* --------------------------------------- */
/* STATUS COLORS */
/* --------------------------------------- */
const getStatusDot = (status) =>
  ({
    approved: "bg-green-500",
    pending: "bg-yellow-500",
    reject: "bg-red-500",
  }[status] || "bg-gray-400");

const getStatusText = (status) =>
  ({
    approved: "text-green-600",
    pending: "text-yellow-600",
    reject: "text-red-600",
  }[status] || "text-gray-600");

/* --------------------------------------- */
/* DOCUMENT POPUP */
/* --------------------------------------- */
const DocumentPopup = ({ user, onClose }) => {
  const aFront = user?.adhaarCard?.frontImage;
  const aBack = user?.adhaarCard?.backImage;
  const pan = user?.panCard?.frontImage;

  return (
    <div
      id="doc-popup-bg"
      onClick={(e) => e.target.id === "doc-popup-bg" && onClose()}
      className="fixed inset-0 bg-black/30 flex items-center justify-center z-[999]"
    >
      <div className="bg-white w-[260px] rounded-xl shadow-lg p-4 border">
        <h3 className="text-center font-semibold text-sm mb-3">Documents</h3>

        <div className="space-y-3 overflow-y-auto max-h-[300px] px-1">
          {aFront && (
            <div className="w-full border rounded-md p-2 shadow">
              <img src={aFront} className="rounded-md w-full" />
              <p className="text-center text-xs mt-1 font-medium">Aadhar Front</p>
            </div>
          )}

          {aBack && (
            <div className="w-full border rounded-md p-2 shadow">
              <img src={aBack} className="rounded-md w-full" />
              <p className="text-center text-xs mt-1 font-medium">Aadhar Back</p>
            </div>
          )}

          {pan && (
            <div className="w-full border rounded-md p-2 shadow">
              <img src={pan} className="rounded-md w-full" />
              <p className="text-center text-xs mt-1 font-medium">PAN Card</p>
            </div>
          )}

          {!aFront && !aBack && !pan && (
            <p className="text-center text-gray-500 text-xs">
              No Documents Submitted
            </p>
          )}
        </div>

        <button
          onClick={onClose}
          className="w-full mt-3 bg-gray-900 text-white rounded-lg py-1.5 text-xs"
        >
          Close
        </button>
      </div>
    </div>
  );
};

/* --------------------------------------- */
/* MAIN PAGE */
/* --------------------------------------- */
export default function UserModerationDashboard() {
  const [users, setUsers] = useState([]);
  const [popupUser, setPopupUser] = useState(null);

  const [search, setSearch] = useState("");
  const [status, setStatus] = useState("Status");
  const [gender, setGender] = useState("Gender");
  const [loading, setLoading] = useState(true);

  const [sortField, setSortField] = useState(null);
  const [sortDirection, setSortDirection] = useState("asc");

  const [currentPage, setCurrentPage] = useState(1);
  const perPage = 10;

  const [counts, setCounts] = useState({
    total: 0,
    pending: 0,
    approved: 0,
    rejected: 0,
  });

  /* --------------------------------------- */
  /* FETCH USERS */
  /* --------------------------------------- */
  useEffect(() => {
    const load = async () => {
      try {
        const res = await fetch(`${API_URL}/admin/user-verify`);
        const json = await res.json();

        setUsers(json.data);

        setCounts({
          total: json.data.length,
          pending: json.data.filter((x) => x.adminApprovel === "pending").length,
          approved: json.data.filter((x) => x.adminApprovel === "approved").length,
          rejected: json.data.filter((x) => x.adminApprovel === "reject").length,
        });
      } catch (err) {
        console.log("API ERROR", err);
      } finally {
        setLoading(false);
      }
    };

    load();
  }, []);

  /* --------------------------------------- */
  /* SORTING LOGIC */
  /* --------------------------------------- */
  const sortData = (data) => {
    if (!sortField) return data;

    return [...data].sort((a, b) => {
      let valA, valB;

      if (sortField === "name") {
        valA = (a.firstName + " " + a.lastName).toLowerCase();
        valB = (b.firstName + " " + b.lastName).toLowerCase();
      }

      if (sortField === "date") {
        valA = new Date(a.createdAt);
        valB = new Date(b.createdAt);
      }

      return sortDirection === "asc" ? valA - valB : valB - valA;
    });
  };

  const toggleSort = (field) => {
    if (sortField === field) {
      setSortDirection(sortDirection === "asc" ? "desc" : "asc");
    } else {
      setSortField(field);
      setSortDirection("asc");
    }
  };

  /* --------------------------------------- */
  /* FILTER + SORT */
  /* --------------------------------------- */
  const filtered = useMemo(() => {
    let d = [...users];

    if (search) {
      d = d.filter(
        (u) =>
          u.firstName?.toLowerCase().includes(search.toLowerCase()) ||
          u.lastName?.toLowerCase().includes(search.toLowerCase()) ||
          u.id?.toLowerCase().includes(search.toLowerCase())
      );
    }

    if (status !== "Status") d = d.filter((u) => u.adminApprovel === status);
    if (gender !== "Gender") d = d.filter((u) => u.gender === gender);

    return sortData(d);
  }, [users, search, status, gender, sortField, sortDirection]);

  /* --------------------------------------- */
  /* PAGINATION */
  /* --------------------------------------- */
  const totalPages = Math.ceil(filtered.length / perPage);
  const pageData = filtered.slice((currentPage - 1) * perPage, currentPage * perPage);

  /* --------------------------------------- */
  /* UI */
  /* --------------------------------------- */
  return (
    <div className="px-6 py-6">

      {/* TOP CARDS */}
      <div className="flex justify-center gap-12 mb-8">
        {[
          ["Total Request This Week", counts.total],
          ["Pending Verification", counts.pending],
          ["Approved This Week", counts.approved],
          ["Rejected Due To Mismatch", counts.rejected],
        ].map(([label, count]) => (
          <div
            key={label}
            className="w-[220px] text-center border shadow rounded-2xl p-6 bg-white"
          >
            <p className="font-semibold text-sm">{label}</p>
            <h2 className="text-3xl font-black mt-3">{count}</h2>
          </div>
        ))}
      </div>

      {/* TABLE */}
      <div className="max-w-7xl mx-auto p-5 border rounded-2xl bg-white shadow">
        {/* SEARCH + FILTER */}
        <div className="border rounded-xl p-3 flex justify-between items-center">
          <div className="relative w-[300px]">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400 w-5" />

            <input
              placeholder="Search By User ID"
              value={search}
              onChange={(e) => setSearch(e.target.value)}
              className="w-full pl-10 pr-3 py-2 border rounded-lg text-sm"
            />
          </div>

          <div className="flex gap-3">
            <select
              className="border px-3 py-2 bg-gray-100 rounded-md"
              value={status}
              onChange={(e) => setStatus(e.target.value)}
            >
              <option>Status</option>
              <option value="approved">Approved</option>
              <option value="pending">Pending</option>
              <option value="reject">Rejected</option>
            </select>

            <select
              className="border px-3 py-2 bg-gray-100 rounded-md"
              value={gender}
              onChange={(e) => setGender(e.target.value)}
            >
              <option>Gender</option>
              <option>Male</option>
              <option>Female</option>
            </select>
          </div>
        </div>

        {/* TABLE */}
        <div className="overflow-x-auto mt-5 border rounded-xl">
          <table className="w-full text-sm">
            <thead className="bg-gray-100">
              <tr>
                <th className="px-4 py-3 text-left cursor-pointer" onClick={() => toggleSort("name")}>
                  <div className="flex items-center gap-1">
                    Reported User
                    {sortField === "name" && (sortDirection === "asc" ? <ChevronUp size={16}/> : <ChevronDown size={16}/>)}
                  </div>
                </th>

                <th className="px-4 py-3 text-left cursor-pointer" onClick={() => toggleSort("date")}>
                  <div className="flex items-center gap-1">
                    Report Date
                    {sortField === "date" && (sortDirection === "asc" ? <ChevronUp size={16}/> : <ChevronDown size={16}/>)}
                  </div>
                </th>

                <th className="px-4 py-3 text-left">Documents Submitted</th>
                <th className="px-4 py-3 text-left">Status</th>
                <th className="px-4 py-3 text-left">Actions</th>
              </tr>
            </thead>

            <tbody className="divide-y">
              {pageData.map((u) => (
                <tr key={u._id} className="hover:bg-gray-50">
                  <td className="px-4 py-3">
                    <UserAvatar user={u} />
                  </td>

                  <td className="px-4 py-3">
                    {new Date(u.createdAt).toLocaleDateString("en-GB", {
                      day: "2-digit",
                      month: "long",
                      year: "numeric",
                    })}
                  </td>

                  <td className="px-4 py-3">
                    {u.adhaarCard?.frontImage ||
                    u.adhaarCard?.backImage ||
                    u.panCard?.frontImage
                      ? "Documents Uploaded"
                      : "No Document Submitted"}
                  </td>

                  <td className="px-4 py-3">
                    <div className="flex items-center gap-2">
                      <div className={`w-3 h-3 rounded-full ${getStatusDot(u.adminApprovel)}`} />
                      <span className={getStatusText(u.adminApprovel)}>{u.adminApprovel}</span>
                    </div>
                  </td>

                  <td className="px-4 py-3">
                    <button
                      className="text-blue-700 flex items-center gap-1"
                      onClick={() => setPopupUser(u)}
                    >
                      <Eye size={14} /> View
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>

        {/* PAGINATION */}
        <div className="flex justify-center mt-4 items-center gap-3">
          <button
            onClick={() => setCurrentPage((p) => Math.max(1, p - 1))}
            disabled={currentPage === 1}
            className="text-gray-700 disabled:opacity-40"
          >
            ‹ Prev
          </button>

          <div className="flex gap-2 text-sm">
            {[1, 2, 3, 4].map(
              (p) =>
                p <= totalPages && (
                  <button
                    key={p}
                    onClick={() => setCurrentPage(p)}
                    className={`${
                      currentPage === p ? "font-bold text-black underline" : "text-gray-600"
                    }`}
                  >
                    {p}
                  </button>
                )
            )}

            {totalPages > 4 && <span className="text-gray-500">.....</span>}

            {totalPages > 4 && (
              <button
                onClick={() => setCurrentPage(totalPages)}
                className={`${
                  currentPage === totalPages ? "font-bold text-black underline" : "text-gray-600"
                }`}
              >
                {totalPages}
              </button>
            )}
          </div>

          <button
            onClick={() => setCurrentPage((p) => Math.min(totalPages, p + 1))}
            disabled={currentPage === totalPages}
            className="text-gray-700 disabled:opacity-40"
          >
            Next ›
          </button>
        </div>
      </div>

      {/* POPUP */}
      {popupUser && <DocumentPopup user={popupUser} onClose={() => setPopupUser(null)} />}
    </div>
  );
}
